
.main-container {
  margin: 20px auto;
  width: 60%;
  display: block;
  background: white;
  min-height: 400px;
  border-left: 2px solid #0077B5;
  border-right: 2px solid #0077B5;
  border-radius: 10px; }

.selectcontainer-initial {
  display: flex;
  justify-content: flex-start;
  flex-wrap: nowrap;
  margin-top: 20px;
  margin-bottom: 20px;
  .new-filter {
    color: grey;
    line-height: 2.4em;
    margin-left: 10px; }

  .beautify {
    width: 30%;
    margin-left: 20px; }
  .operators {
    width: 14%;
    margin-left: 20px; } }

.selectcontainer {
  display: flex;
  justify-content: flex-start;
  flex-wrap: nowrap;
  .beautify {
    width: 30%;
    margin-left: 20px; }
  .operators {
    width: 14%;
    margin-left: 20px; } }


.header {
  padding: 20px;
  color: #0077B5;
  font-size: 26px; }


.g-button {
  margin: 10px 0 10px 21px;
  min-width: 100px; }

.infos {
  display: flex;
  justify-content: flex-start;
  flex-wrap: nowrap;
  span {
    margin: -5px 0 10px 21px;
    text-align: center;
    position: relative;
    min-width: 100px;
    font-size: 12px; } }

.p-info {
  color: grey;
  margin: 0 0 0px 21px;
  font-size: 14px;
  position: relative;
  top: 7px; }

.p-info2 {
  color: grey;
  margin: 0 0 0px 21px;
  font-size: 14px; }

.new-div {
  border-top: 1px dashed lightgrey;
  margin-top: 20px;
  padding-top: 20px;
  margin-bottom: 20px; }

.x-button {
  margin-left: auto;
  margin-right: 40px;
  width: 30px;
  height: 30px; }

.search-button {
  margin: auto;
  display: block; }
